import { useState } from 'react';
import TransactionRequest from '../../../model/TransactionRequest'
import TransactionRequestHolder from './TransactionRequestHolder'

export class TransactionRequestState {
  static useState(): [TransactionRequest, (tr: TransactionRequest) => void] {
    const [transactionRequest, _setTransactionRequest] = useState(TransactionRequestHolder.get() || new TransactionRequest())
    TransactionRequestHolder.set(transactionRequest)
    const setTransactionRequest = (tr: TransactionRequest) => {
      const newTransactionRequest = {...tr}
      TransactionRequestHolder.set(newTransactionRequest)
      _setTransactionRequest(newTransactionRequest)
    }
    return [transactionRequest, setTransactionRequest]
  }
}

export default TransactionRequestState