import axios from 'axios'

const basePriceUrl = 'https://api.coingecko.com/api/v3/simple/price?ids=$COIN&vs_currencies=btc'

const priceUrls = {
  BTCP: basePriceUrl.replace('$COIN', 'bitcoin-private'),
  BTG: basePriceUrl.replace('$COIN', 'bitcoin-gold'),
  MNX: basePriceUrl.replace('$COIN', 'minexcoin'),
  KMD: basePriceUrl.replace('$COIN', 'komodo'),
  ZEN: basePriceUrl.replace('$COIN', 'zencash'),
  ZEL: basePriceUrl.replace('$COIN', 'zelcash')
}

const _setBtcPrice = (coin, p) => {
  localStorage.setItem(coin+'-price', JSON.stringify(p))
}

const _getBtcPrice = (coin) => {
  const val = localStorage.getItem(coin+'-price')
  if (val) return JSON.parse(val)
  else return void(0)
}

const getBtcPrice = async (coin) => {
  try {
    const _price = _getBtcPrice(coin)
    if (_price && Date.now() < _price.timeout) {
      return _price.btc
    }
    if (priceUrls[coin]) {
      const res = await axios.get(priceUrls[coin])
      const key = Object.keys(res.data)[0]
      const price = {
        btc: parseFloat(res.data[key].btc),
        timeout: Date.now() + (1000*60*10)
      }
      _setBtcPrice(coin, price)
      return price.btc
    }
  } catch (err) {
    console.error(err)
    const _price = _getBtcPrice(coin)
    if (_price) {
      return _price.btc
    }
    return void 0
  }
  return void(0)
}

export {
  getBtcPrice
}