import React, { FC } from 'react';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import BaseStyle from '../../style/BaseStyle';
import CoinImages from '../../images/CoinImages';
import DropDown from './DropDown';
import formatNumber from './formatNumber';
import {SrcCoinTypes, Coin} from '../../../../model/Coin';
import TransactionRequest from '../../../../model/TransactionRequest'

interface Props {
  style?: React.CSSProperties
  getAndSetBtcPrice: (coin: Coin) => void
  transactionRequest: TransactionRequest
  setTransactionRequest: (tr: TransactionRequest) => void
}

const style = new BaseStyle();

export const YouSend: FC<Props> = (props) => {

  const {transactionRequest, setTransactionRequest} = props
  const srcCoinImage = CoinImages[transactionRequest.srcCoin].small

  return (
    <Grid container item xs={12} sm={6} style={props.style}>

      <div style={{
        ...style.centerRow,
        border: '1px solid Magenta',
        borderRadius: 5,
        width: '100%',
        marginLeft: '4%',
        marginRight: '4%',
        marginBottom: '4%',
        // justifyContent: 'flex-start'
      }}>

        <div style={{
          // paddingLeft: 15,
          // border: '1px solid RED',
          width: '50%',
          paddingLeft: '5%'
        }}>
        <TextField
          inputProps={{style: {fontSize: 26}}}
          InputLabelProps={{style: {fontSize: 20}}}
          type="number"
          id="name"
          label="You Send"
          margin="none"
          value={formatNumber(transactionRequest.amount) || ''}
          onChange={e => {
            transactionRequest.amount = parseFloat(e.target.value)
            setTransactionRequest(transactionRequest)
          }}
        />
        </div>

        {/* <Divider orientation="vertical" flexItem /> */}

        <div style={{
          // paddingLeft: 15,
          // border: '1px solid RED',
          // paddingRight: 15,
          ...style.centerRow,
          justifyContent: 'flex-start',
          width: '50%',
          paddingLeft: '5%'
        }}>

        <img style={{
          width: 32, 
          height: 32,
          // border: '1px solid RED',
          marginTop: 14,
          marginRight: 5
        }} src={srcCoinImage} />

        <DropDown
          inputProps={{style: {
            fontSize: 26,
            // border: '1px solid GREEN',
            height: 44,
            paddingTop: 0,
            paddingBottom: 0,
          }}}
          name={''}
          label={'Coin'}
          style={{
            formControl: {
              width: '100%',
              minWidth: 85,
            }
          }}
          value={transactionRequest.srcCoin || ''}
          onChange={(e) => {
            transactionRequest.srcCoin = e.target.value
            setTransactionRequest(transactionRequest)
            props.getAndSetBtcPrice(transactionRequest.srcCoin)
          }}
          options={SrcCoinTypes}
        />
        </div>

      </div>

    </Grid>
  )

}

export default YouSend