import React, { FC } from 'react';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import BaseStyle from '../../style/BaseStyle';
import CoinImages from '../../images/CoinImages';
import formatNumber from './formatNumber';
import TransactionRequest from '../../../../model/TransactionRequest'

interface Props {
  style?: React.CSSProperties
  transactionRequest: TransactionRequest
  setTransactionRequest: (tr: TransactionRequest) => void
}

const style = new BaseStyle();

export const PayoutAddress: FC<Props> = (props) => {

  const {transactionRequest, setTransactionRequest} = props
  const dstCoinImage = CoinImages[transactionRequest.dstCoin].small

  const itemStyle = {
    // border: '1px solid BLACK',
    height: 120,
    width: '100%',
    justifyContent: 'center',
    
  }

  return (
    <Grid container item xs={12} sm={12} style={itemStyle}>

      <div style={{
        ...style.centerRow,
        border: '1px solid Magenta',
        borderRadius: 5,
        width: '100%',
        marginLeft: '4%',
        marginRight: '4%',
        marginBottom: '2%',
        maxWidth: 520,
        // justifyContent: 'flex-start'
      }}>

        <div style={{
          ...style.centerRow,
          // paddingLeft: 15,
          // border: '1px solid RED',
          width: '100%',
          paddingLeft: '5%'
        }}>

        <img style={{
          width: 32, 
          height: 32,
          // border: '1px solid RED',
          marginTop: 14,
          marginRight: 5
        }} src={dstCoinImage}/>

        <TextField
          style={{width: '90%', paddingRight: 30}}
          inputProps={{style: {fontSize: 18, border: '0px solid RED', height: 35}}}
          InputLabelProps={{style: {fontSize: 20}}}
          label="Recipient BTC Address"
          margin="none"
          value={transactionRequest.dstAddress}
          onChange={e => {
            transactionRequest.dstAddress = e.target.value.trim()
            setTransactionRequest(transactionRequest)
          }}
        />
        </div>

      </div>

    </Grid>
  )

}

export default PayoutAddress