import React, { FC } from 'react';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import BaseStyle from '../../style/BaseStyle';
import CoinImages from '../../images/CoinImages';
import formatNumber from './formatNumber';
import TransactionRequest from '../../../../model/TransactionRequest'

interface Props {
  style?: React.CSSProperties
  transactionRequest: TransactionRequest
  btcPrice: number
}

const style = new BaseStyle();

export const YouGet: FC<Props> = (props) => {

  const {transactionRequest, btcPrice} = props
  const srcAmount = formatNumber(transactionRequest.amount)
  const dstAmount = formatNumber(srcAmount * btcPrice * 0.992)
  const dstCoinImage = CoinImages[transactionRequest.dstCoin].small

  return (
    <Grid container item xs={12} sm={6} style={props.style}>

      <div style={{
        ...style.centerRow,
        border: '1px solid Magenta',
        borderRadius: 5,
        width: '100%',
        marginLeft: '4%',
        marginRight: '4%',
        marginBottom: '4%',
        // justifyContent: 'flex-start'
      }}>

        <div style={{
          // ...style.centerRow,
          // paddingLeft: 15,
          // border: '1px solid RED',
          width: '50%',
          paddingLeft: '5%'
        }}>

        <TextField
          disabled
          inputProps={{style: {fontSize: 26}}}
          InputLabelProps={{style: {fontSize: 20}}}
          type="number"
          label="You Get"
          margin="none"
          value={dstAmount || ''}
          // onChange={e => {
          // }}
        />
        </div>

        {/* <Divider orientation="vertical" flexItem /> */}

        <div style={{
          // ...style.centerRow,
          // paddingLeft: 15,
          // border: '1px solid RED',
          // paddingRight: 15,
          // marginRight: '10%',
          width: '50%',
          ...style.centerRow,
          justifyContent: 'flex-start',
          paddingLeft: '5%',
        }}>

        <img style={{
          width: 32, 
          height: 32,
          // border: '1px solid RED',
          marginTop: 14,
          marginRight: 5
        }} src={dstCoinImage}/>

        <TextField
          disabled
          style={{
            width: '45%', 
            // border: '1px solid GREEN'
          }}
          inputProps={{style: {paddingTop: 20, fontSize: 26, border: '0px solid RED', height: 32}}} 
          id="name"
          margin="none"
          value={'BTC'}
          // onChange={e => {
          // }}
        />
        </div>

      </div>

    </Grid>
  )

}

export default YouGet