import TransactionRequest from '../../../model/TransactionRequest'

const TRANSACTION_REQUEST_KEY = 'TRANSACTION_REQUEST_KEY';

export class TransactionRequestHolder {

  static transactionRequest: TransactionRequest | undefined;

  static set(transactionRequest: TransactionRequest | undefined) {
    TransactionRequestHolder.transactionRequest = transactionRequest
    if (transactionRequest) {
      sessionStorage.setItem(TRANSACTION_REQUEST_KEY, JSON.stringify(transactionRequest))
    }
    else {
      sessionStorage.removeItem(TRANSACTION_REQUEST_KEY)
    }
  }

  static get() {
    if (!TransactionRequestHolder.transactionRequest) {
      const value = sessionStorage.getItem(TRANSACTION_REQUEST_KEY);
      if (value) {
        TransactionRequestHolder.transactionRequest = JSON.parse(value)
      } 
    }
    return TransactionRequestHolder.transactionRequest
  }

}

export default TransactionRequestHolder