import React, { FC, useEffect, useState } from 'react';
import TransactionState from './TransactionRequestState';
import Coin from '../../../model/Coin';
import Grid from '@material-ui/core/Grid';
import {getBtcPrice} from '../../helpers/CoinHelper';
import YouSend from './components/YouSend'
import YouGet from './components/YouGet'
import PayoutAddress from './components/PayoutAddress'
import SubmitButton from '../core/SubmitButton'
import BaseStyle from '../style/BaseStyle';
import './form.css';

const style = new BaseStyle();

const _getAndSetBtcPrice = (setBtcPrice, coin: Coin) => {
  getBtcPrice(coin)
    .then(price => setBtcPrice(price))
    .catch(err => console.error(err))
}

// tslint:disable-next-line: variable-name
export const TransactionRequestForm: FC<{}> = () => {

  const [btcPrice, setBtcPrice] = useState(0)
  const [transactionRequest, setTransactionRequest] = TransactionState.useState();
  const getAndSetBtcPrice = _getAndSetBtcPrice.bind(null, setBtcPrice)

  useEffect(() => {
    getAndSetBtcPrice(transactionRequest.srcCoin)
  }, [btcPrice])

  const containerStyle = {
    // border: '1px solid red',
    maxWidth: 800
  }

  const itemStyle = {
    // paddingBottom: 4,
    // border: '1px solid BLACK',
    height: 120,
    width: '100%',
  }

  return <Grid container style={containerStyle}>

    <YouSend 
      style={itemStyle}
      setTransactionRequest={setTransactionRequest} 
      transactionRequest={transactionRequest} 
      getAndSetBtcPrice={getAndSetBtcPrice} 
    />

    <YouGet 
      style={itemStyle} 
      transactionRequest={transactionRequest} 
      btcPrice={btcPrice}
     />

    <PayoutAddress 
      style={itemStyle}
      setTransactionRequest={setTransactionRequest} 
      transactionRequest={transactionRequest} 
    />

    <div style={{
      ...style.center, 
      width: '100%', 
      // border: '1px solid RED',
      paddingBottom: 4
    }}>

    <SubmitButton 
      style={{width: 200, height: 44}}
      buttonText={'exchange'}
      enabled={true} 
      onClick={(e) => {
        
      }}
    />

    </div>

  </Grid>
};

export default TransactionRequestForm
